import React, {useEffect, useCallback, useRef} from 'react';
import Kind2 from '../components/Kind2';
import { useSelector, useDispatch } from 'react-redux';
import { actions } from '../redux/actions'

function Kind2Container() {
    const dispatch = useDispatch();
    const { list_kind1, kind2, user_chk_kind2 } = useSelector(state => state.listStates)

    const onUserChkKind2 = (e) => dispatch(actions.userChkKind2(e));
    const onGetListKind2 = (e) => dispatch(actions.getListKind2(e));
    const resetCounting = () => dispatch(actions.setListCounting(5));
    const clickButton = (checkbox) => {
        resetCounting();
        changeSkillList(checkbox)
    };

    // kind1 ÌÅ¥Î¶≠Ïãú kind2 button active styleÏùÑ Î™®Îëê Ìï¥Ï†úÌïòÍ∏∞ ÏúÑÌï¥ storeÏóê Îì±Î°ù
    const kind2InputRef = useRef([]);
    const onGetInputRef = (refs) => dispatch(actions.getInputRef(refs));
    useEffect(() => onGetInputRef(kind2InputRef) , [kind2InputRef]);

    
    /**
     * @name changeSkillList
     * @param {checkbox} 
     * @description Ïú†Ï†ÄÍ∞Ä ÌôîÎ©¥ÏóêÏÑú Ï≤¥ÌÅ¨Ìïú Ïä§ÌÇ¨ Î¶¨Ïä§Ìä∏ storeÏóê Îì±Î°ù
     */
    const changeSkillList = (checkbox) => {
        if( checkbox.target.value == undefined ) return;
        const userchk_skill = user_chk_kind2.slice(); // Î≥µÏÇ¨Ìï¥ÏÑú ÏÇ¨Ïö©
        const indexof = userchk_skill.indexOf(checkbox.target.value); 
        
        if( checkbox.target.checked === true && indexof === -1 ) {       // kindÏÑ†ÌÉù ÌñàÍ≥†, Î∞∞Ïó¥ÏïàÏóê ÏóÜÎã§Î©¥
            userchk_skill.push(checkbox.target.value); // Ï∂îÍ∞Ä
        }else if( checkbox.target.checked === false && indexof !== -1 ){ // kindÏÑ†ÌÉù ÏßÄÏõ†Í≥†, Î∞∞Ïó¥ÏïàÏóê ÏûàÎã§Î©¥
            userchk_skill.splice( indexof, 1 ); // Ï†úÍ±∞
        }
        kind2Calculator(userchk_skill);
    }


    /**
     * @name kind2Calculator 
     * @param {Ïú†Ï†ÄÍ∞Ä ÌôîÎ©¥ÏÉÅÏóê Ï≤¥ÌÅ¨Ìïú Ïä§ÌÇ¨} userchk_skill 
     * @description ÌôîÎ©¥ÏÉÅÏóê ÎùÑÏö∏ Î¶¨Ïä§Ìä∏Ïù∏ list_kind2 Î•º Í≥ÑÏÇ∞
     *              1) Ïú†Ï†ÄÍ∞Ä Ï≤¥ÌÅ¨Ìïú Ïä§ÌÇ¨Ïóê Î™®Îëê Ìè¨Ìï®ÎêòÎäîÏßÄ, Í∞Å Î¶¨Ïä§Ìä∏ Î™®Îëê Ï≤¥ÌÅ¨
     *              2) Ï§ëÎ≥µÍ∞í Î™®Îëê Ï†úÍ±∞
     */
    const kind2Calculator = (userchk_skill) => {
        try{
            let listResult = [];

            for(let k=0; k<list_kind1.length; k++){
                let chk = false; // chk => 2) Ï§ëÎ≥µÍ∞í Î™®Îëê Ï†úÍ±∞ 
                for(let i=0; i<userchk_skill.length; i++){
                    if(list_kind1[k].kind2.indexOf(userchk_skill[i]) !== -1){
                        chk = true;
                    }else{
                        chk = false;
                        break;
                    }
                }
                if( chk === true ) listResult.push(list_kind1[k]) // Ï§ëÎ≥µ ÏïÑÎãàÎ©¥ push
            }
            
            onUserChkKind2(userchk_skill);
            onGetListKind2(listResult);
            // console.log("üöÄ ~ file: kind2Container.js ~ line 69 ~ kind2Calculator ~ listResult", listResult)
        }catch(error){
            console.log(error)
        }
    }


    return (
        <Kind2
            kind2={kind2}
            clickButton={clickButton}
            inputRef={kind2InputRef}
        />
    );
}

export default Kind2Container;
